import { getFormattedDiff } from '../utils/time';
const markUpOffer = ({ title, price }) => `<li class="event__offer">
<span cllass="event__offer-title">${title}</span>
nbsp;
<span class="event__offer-price">${price}</span>
</li>`;
const markUpOffers = (offers) => `
<h4 class="visually-hidden">Offers:</h4>
<ul class="event__selected-offers">
${offers.map(markUpOffer).join('')}
</ul>
`;
const markUpTime = (date) => `<time class="event__start-time" datetime="${date.format('YYYY-MM-DD[T]HH:mm')}">${date.format('H:m')}</time>`;
export function markUpEvent({ city, type, basePrice, offers, dateFrom, dateTo, isFavorite }) {
    const offersMarkup = offers.length ? markUpOffers(offers) : '';
    const favoriveClasses = ['event__favorite-btn'];
    if (isFavorite) {
        favoriveClasses.push('event__favorite-btn--active');
    }
    return `<div class="event">
    <time class="event__date" datetime="${dateFrom.format('YYYY-MM-DD[T]HH:mm')}">${dateFrom.format('MMM D')}</time>
    <div class="event__type">
      <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="${type} icon">
    </div>
    <h3 class="event__title">${type} ${city}</h3>
    <div class="event__schedule">
      <p class="event__time">
       ${markUpTime(dateFrom)}
       -
       ${markUpTime(dateTo)}
      </p>
      <p class="event__duration">${getFormattedDiff(dateFrom, dateTo)}</p>
    </div>
    <p class="event__price">
      &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
    </p>
   ${offersMarkup}
    <button class="${favoriveClasses.join(' ')}" type="button">
      <span class="visually-hidden">Add to favorite</span>
      <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
        <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
      </svg>
    </button>
    <button class="event__rollup-btn" type="button">
      <span class="visually-hidden">Open event</span>
    </button>
  </div>`;
}
