import { CITIES } from '../constants';
import { getRandomElement, getRandomInteger } from '../utils/random';
const MESSAGES = ['Lorem ipsum dolor sit amet, consectetur adipiscing elit. Cras aliquet varius magna, non porta ligula feugiat eget.'];
const mockDescription = () => Array.from({ length: getRandomInteger(5 /* Description.MAX */ - 1 /* Description.MIN */) }, () => getRandomElement(MESSAGES)).join(' ');
const mockPicture = (city) => ({
    src: `https://loremflickr.com/248/152/${city}?random=${getRandomInteger()}`,
    description: mockDescription(),
});
const mockDestination = (name) => ({
    id: crypto.randomUUID(),
    description: mockDescription(),
    name,
    pictures: Array.from({ length: getRandomInteger(1, 5) }, () => mockPicture(name)),
});
export const mockDestinations = () => CITIES.map(mockDestination);
