export default class PointsModel {
    #service = null;
    #points = [];
    constructor(service) {
        this.#service = service;
        this.#points = this.#service.getPoints();
    }
    get points() {
        return this.#points;
    }
    getById(id) {
        return this.#points.find((point) => point.id === id);
    }
}
